import avatar from "@/components/yq-avatar/yq-avatar.vue";
import htzImageUpload from '@/components/htz-image-upload/htz-image-upload.vue'
export default {
	components: {
		avatar,
		htzImageUpload,
	},
	data() {
		return {
			uploadParams: {
				type: 'dailyLife'
			},
			problemList:[{
				id:1,
				title:'系统操作问题'
			},{
				id:2,
				title:'平台优化建议'
			},{
				id:3,
				title:'违规内容举报'
			},{
				id:4,
				title:'内容方向建议'
			},{
				id:5,
				title:'平台bug上报'
			},{
				id:6,
				title:'其他内容'
			},],
			currentTab:-1,
			urls: [],
			urlImgIndex: 0,
			showAvatar: false,
			imageData: [],
		};
	},
	methods:{
		getMemberSignRecord(){
			let memberId = uni.getStorageSync('userInfo') ? JSON.parse(uni.getStorageSync('userInfo')).id : ''
			if(uni.getStorageSync('userInfo')){
				this.$Request.postT('/appSignController.do?getMemberSignRecord',{
					memberId: this.memberId
				}).then(res => {
					if(res.code == 0){
						this.trainInfo = res.data
						this.checks = res.data.signList
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		clickTab(item, index){
			this.currentTab = index
		},
		ceshiChooseSuccess(tempFilePaths) { //选择图片返回
			console.log('ceshiChooseSuccess', tempFilePaths);
			/****************
			以下代码是自定义上传逻辑，仅供参考
			***************/
			this.imgUpload(tempFilePaths);
			/*******************************/
		},
	}
}
